export async function fetchImages(query, page = 1) {
  try {
    const response = await axios.get(BASE_URL, {
      params: {
        key: API_KEY,
        q: query,
        image_type: 'photo',
        orientation: 'horizontal',
        safesearch: true,
        per_page: PER_PAGE, // ‚úÖ –î–æ–¥–∞—î–º–æ –æ–±–º–µ–∂–µ–Ω–Ω—è –Ω–∞ 15 –∑–æ–±—Ä–∞–∂–µ–Ω—å
        page: page, // ‚úÖ –ü–µ—Ä–µ–¥–∞—î–º–æ –Ω–æ–º–µ—Ä —Å—Ç–æ—Ä—ñ–Ω–∫–∏
      },
    });
    return response.data.hits;
  } catch (error) {
    console.error('Error fetching images:', error);
    return [];
  }
}
